.Project{
    position: relative;
    top: 5rem;
    color: #fff;
    text-align: center;
    align-items: center;
}

.card{
    width: 18rem;
    margin: 1rem 1rem;
    border-radius: 1rem;
    height: 25rem;
}
.col{
    margin: 1rem 1rem;
}

@media screen and (max-width: 768px) {
    .card{
        margin: auto;
    }
}

.card-img-top{
    padding: 0.5rem;
    background-color: black;
}

.card:hover{
    transform: scale(1.1);
    transition: 0.2s ease-in-out;
}


.edit{
    background-color: aliceblue;
    padding: 1rem;
    border-radius: 1rem;
}

.edit input{
    border: #000 solid 4px !important;
}

