@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@900&display=swap');
.Contact{
    margin-top: 7%;
    color: antiquewhite;
    text-align: center;
    align-items: center;
    align-content: center;
    font-family: 'Orbitron', sans-serif;
    
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
    align-items: center;
    margin: 0rem auto;
}

.form input{
    margin: 1rem;
    border-radius: 1rem;
    border: none;
    padding: 1rem;
    font-weight: 800;
}

.form input:focus{
    background-color:#007bff;
    font-weight: 800;
    color: white;
}

.form input:focus::placeholder{
    color: aliceblue;
}


.form textarea{
    height: 5rem;
    border-radius: 1rem;
    text-align: center;
    align-items: center;
    font-weight: 800;
    padding: 1rem;
}
.form textarea:focus::placeholder{
    text-align: center;
    color: aliceblue;
    
}
.form textarea:focus{
    text-align: left;
    background-color:#007bff;
}

.button{
    padding: 0.5rem;
    margin: 1rem auto;
    width: 50%;
    background-color:#007bff;
    font-size: larger;
}

@media screen and (max-width: 768px) {
    .Contact{
        margin-top: 10% ;
    }
    .form{
        display: flex;
        flex-direction: column;
        width: 50%;
        margin: 2rem auto;
    }
    .form textarea{
        height: 5rem;
        border-radius: 1rem;
        text-align: center;
        align-items: center;
        font-weight: 800;
        padding: 1rem;
    }

    .form{
        font-size: 0.8rem;
    }
    .form label{
        margin: 0.5rem;
    }

}